.nav {
  /* border: 1px solid black; */
  -webkit-box-shadow: 0 5px 15px 2px rgba(0, 0, 0, 0.5);
          box-shadow: 0 5px 15px 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  position: fixed;
  width: 100%;
  background: white;
  opacity: 0.9;
}
.nav_ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas: 'nav_ul_li_1 nav_ul_li_2 nav_ul_li_3 nav_ul_li_4';
  height: var(--nav-height);
  /* grid-gap: 20px; */
  /* border: 1px solid black; */
  width: 100%;
  margin: 0 auto;
  padding-left: 60%;
  padding-right: 2%;
  font-weight: bold;

  /* position: fixed; */
  /* z-index: 1000; */
}

.nav_ul li {
  /* border: 1px solid red; */
  list-style: none;
  height: 100%;
  display: block;
}

.a {
  color: black;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  display: block;
  height: 100%;
  width: 100%;
  text-align: center;
  line-height: calc(var(--nav-height));
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.nav_ul li a:visited {
  color: none;
  text-decoration: none;
}
.active {
  /* box-shadow: 0 1px 30px 1px rgba(0, 0, 0, 0.3); */
  /* color: blueviolet; */
  color: #75489f;
  /* text-decoration: underline; */
}

.nav_ul_li_1 {
}
.nav_ul_li_2 {
}
.nav_ul_li_3 {
}
.nav_ul_li_4 {
}
/* .pageWrapper 
  position: absolute;
  top: var(--nav-height);
} */

@media screen and (max-width: 660px) {
  .nav_ul {
    display: grid;
    grid-template-columns: 100%;
    grid-template-areas:
      'nav_ul_li_1'
      'nav_ul_li_2'
      'nav_ul_li_3'
      'nav_ul_li_4';
    height: var(--nav-height-660px);
    grid-gap: 2px;
    /* border: 1px solid red; */
    width: 100%;
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
  }
}



